@font-face {
    font-family: 'icon';
    src: url(/blog/static/media/iconfont.22142ed2.eot);
    src: url(/blog/static/media/iconfont.22142ed2.eot?#iefix) format('embedded-opentype'),
        url(/blog/static/media/iconfont.885d4a71.woff2) format('woff2'),
        url(/blog/static/media/iconfont.e5cbc9ea.woff) format('woff'),
        url(/blog/static/media/iconfont.8d0c5f87.ttf) format('truetype'),
        url(/blog/static/media/iconfont.a81a2b2c.svg#iconfont) format('svg');
}
.icon{
    font-family: "icon";
    min-width:14px;
    margin-right:10px;
    font-size: 14px;
    -webkit-transition: font-size 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), margin 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: font-size 0.15s cubic-bezier(0.215, 0.61, 0.355, 1), margin 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
}
.logo{
    height:80px;
    color: white;
    text-align: center;
    line-height: 80px;
    letter-spacing: 20px;
    font-size: 18px;
    font-weight: bolder;
    text-shadow: 3px 3px 8px rgb(0, 204, 255)
}
.logo::first-letter{
    font-size: 26px;
}
